import React,{useEffect} from 'react'
import ScreenHeading from '../../utilities/ScreenHeading/ScreenHeading'
import ScrollService from '../../utilities/ScrollService'
import Animations from '../../utilities/Animations'
import './AboutMe.css'

export default function AboutMe(props) {
    let fadeInScreenHandler = (screen) => {
      if(screen.fadeInScreen !== props.id)
      return
      Animations.animations.fadeInScreen(props.id)
    }
    const fadeInSubscription = ScrollService.currentScreenFadeIn.subscribe(fadeInScreenHandler);

    const SCREEN_CONSTANTS = {
      description: "Interested and experienced in different fields ...",
      highlights:{
        bullets:[ 
          "Front- and backend development",
          "Webdesign: Low and High Fidelity with Axure and Figma",
          "Interactive Computer Graphics",
          "Developing with Game Engines like UE5 and Unity",
          "Modelling, texturing and animating with 3D-Tool Blender ",
          "Augmented Reality and Virtual Reality",
          "AI and Machine Learning",
          "Adobe Creative Cloud (After Effects, Premiere, Photoshop)",
        ],
        heading: "Here are a few Highlights:"

      }
    }
    const renderHighlight = ()=>{
      return(
        SCREEN_CONSTANTS.highlights.bullets.map((value, i)=>(
          <div className='highlight' key={i}>
            <div className='highlight-blob'></div>
            <span>{value}</span>
          </div>
        )
        )
      )
    }


  return (
    <div className='about-me-container screen-container fade-in' id={props.id || ""}>
      <div className='about-me-parent'>
        <ScreenHeading title= {'About Me'} subHeading={'Why Choose Me?'}/>
        <div className='about-me-card'>
          <div className='about-me-profile'></div>
          <div className='about-me-details'>
            <span className='about-me-description'>{SCREEN_CONSTANTS.description}</span>
            <div className='about-me-highlights'>
              <div className='highlight-heading'>
                <span>{SCREEN_CONSTANTS.highlights.heading}</span>
              </div>
              {renderHighlight()}
            </div>
            <div className='about-me-options'>
              <button className='btn primary-btn' onClick={() => ScrollService.scrollHandler.scrollToHireMe()}>Contact Me </button>
              <a href='2022_05_lebenslauf.pdf' download='CV_John_Nitsche'>
                <button className='btn highlighted-btn'>Get my CV</button>
              </a>
            </div>
          </div>
        </div>
      </div>

    </div>
  )
}
